<template><div><h1>VUX v2.0.0 发布-基于 WeUI 和 Vue 的移动端组件库</h1>
<h2>Actionsheet</h2>
<ul>
<li>[change] 更新到 vue@2.0，使用 <code>v-model</code> 而不是<code>:show.sync</code>进行显示属性绑定</li>
<li>[feature] 添加属性 <code>close-on-clicking-mask</code>, 适用于强制选择的场景</li>
</ul>
<h2>Alert</h2>
<ul>
<li>[change] 使用 <code>v-model</code> 而不是<code>:show.sync</code>进行显示属性绑定</li>
<li>[todo] 修复动画</li>
</ul>
<h2>ButtonTab</h2>
<ul>
<li>[enhance] 去除 Android 下点击可能出现的蓝色边框</li>
</ul>
<h2>Checker</h2>
<ul>
<li>[feature] 支持Object类型的值 #705</li>
</ul>
<h2>Checklist</h2>
<ul>
<li>[change] 默认不显示错误，你可以监听on-error事件结合slot进行处理和显示</li>
<li>[feature] 添加插槽 footer、after-title</li>
<li>[change] 如果已经达到max上限，没有选中的选项将不能选择，因此不再和之前版本一样会出现最多可选max个的error信息</li>
<li>[change] 默认 required 值为false, 与html规范一致</li>
</ul>
<h2>ColorPicker</h2>
<ul>
<li>[deprecated] 废弃，当前版本后不再继续维护</li>
</ul>
<h2>Countdown</h2>
<ul>
<li>[deprecated] 下一版本开始不再维护</li>
</ul>
<h2>Countup</h2>
<ul>
<li>[feature] 添加 prop:<code>start</code>, 现在你可以手动开始计数了</li>
<li>[change] 更新依赖<code>countup</code>为<code>countup.js</code></li>
</ul>
<h2>FormPreview</h2>
<ul>
<li>[feature] 增加<code>form-preview</code>组件</li>
</ul>
<h2>LoadMore</h2>
<ul>
<li>[feature] 增加<code>load-more</code>组件</li>
</ul>
<h2>Popover</h2>
<ul>
<li>[todo] 当切换i18n语言时，位置不正确</li>
</ul>
<h2>Toast</h2>
<ul>
<li>[change] 使用 <code>v-model</code> 而不是<code>:show.sync</code>进行显示属性绑定</li>
<li>[change] 默认不显示遮罩</li>
<li>[feature] 添加 isShowMask 属性</li>
</ul>
<h2>XInput</h2>
<ul>
<li>[feature] 添加 <code>novalidate</code> 及 <code>iconType</code> 可以禁用组件验证，手动显示 error 或者 success 样式</li>
<li>[feature] <code>is-type</code> 支持传入函数</li>
<li>[change] 受限于vue2不能动态设置type，目前<code>type</code>支持 text password number email</li>
</ul>
<h2>XNumber</h2>
<ul>
<li>[change] fillable 值默认为false, 稍微保护一下不小心商城的后端接口没有做好数据校验的用户，导致用户粘贴负数还提交正确</li>
</ul>
</div></template><script>
